html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
    font-size: 62.5%;
    height: 100vh;
    background: lightgrey;
}

#h1-report-queue {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
  font-size: 4.8rem;
  margin: 2rem;
}

#report-queue-heading{
  display: flex;
  margin: 1rem;
}

.report-queue-types {
  font-family: Raleway, 'Times New Roman', Times, serif;
  font-size: 2rem;
}

.report-queue-cards {
  display: flex;
  margin: 1rem;
  border: 3px solid black;
  background: darkgray;
}

.actions {
  display: flex;
  flex-direction: column;
}

.report-queue-data {
  font-size: 2rem;
}

.rep-type {
  width: 10%;
  text-align: center;
}

.rep-num {
  width: 10%;
  text-align: center;
}

.rep-reas {
  width: 10%;
  text-align: center;
}

.rep-auth {
  width: 10%;
  text-align: center;
}

.rep-date {
  width: 10%;
  text-align: center;
}

.rep-post {
  width: 25%;
  text-align: center;
  overflow-wrap: break-word;
}

.rep-rep {
  width: 15%;
  text-align: center;
  overflow-wrap: break-word;
}

.rep-act {
  width: 10%;
  text-align: center;
  overflow-wrap: break-word;
}

.button-report-queue {
  text-decoration: none;
    color: black;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    cursor: pointer;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.button-report-queue:hover {
  color: white;
  background: black;
  transition: 0.25s;
}

.buttons-report-queue-nav {
  text-decoration: none;
  color: black;
  display: block;
  font-size: 1.8rem;
  cursor: pointer;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin: 0.5rem;
}

.buttons-report-queue-nav:hover {
  color: white;
  background: black;
  transition: 0.25s;
}
#report-nav-buttons {
  display: flex;
  margin: 0 auto;
  width: 20rem;
  justify-content: space-evenly;
}